@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400..700&family=Raleway:ital,wght@0,100..900;1,100..900&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

html {
  scroll-behavior: smooth;
}

html, body {
  height: 100%;
  background-color: black; 
  color: white;
  margin: 0; 
}

h1, h2 { 
  @apply text-4xl font-bold text-transparent bg-clip-text bg-gradient-to-r from-orange-400 to-pink-600 inline-block; 
  font-family: 'Dancing Script', cursive;
}

.my-custom-class {
  @apply text-pink-800; 
}

.bg-primary-color {
  @apply px-1 py-2 w-full rounded-xl mr-4 bg-gradient-to-r from-orange-500 to-pink-500 text-black font-bold hover:border-none; 
}
.navbar {
  background-color: black;
  padding: 1rem;
  z-index: 10;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 50;
}

.footer {
  background-color: black;
  padding: 1rem;
  z-index: 10;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 50;
}

section {
  min-height: 75vh; 
  padding-top: 4rem; 
  padding-bottom: 4rem; 
}
section.hero {
  padding-top: 6rem; 
}
section.skills, section.about {
  padding: 2rem;
  padding-bottom: 2rem;
}
.flex-container {
  @apply flex flex-col min-h-screen; 
}
@media (min-width: 1024px) {
  section {
    padding-top: 6rem; 
    padding-bottom: 6rem; 
  }
  
  .navbar, .footer {
    padding: 1.5rem; 
  }

  section:not(.hero) {
    margin-top: 2rem; 
  }
}
@media (max-width: 640px) {
  .navbar, .footer {
    padding: 0.5rem; 
  }

  section {
    padding-top: 2rem;
    padding-bottom: 2rem; 
    margin-top: 1rem; 
  }
  section.hero {
    padding-top: 4rem;
  }
}
